@font-face {
  font-family: 'aicon';
  src:  url('fonts/aicon.eot?gwz9im');
  src:  url('fonts/aicon.eot?gwz9im#iefix') format('embedded-opentype'),
    url('fonts/aicon.woff2?gwz9im') format('woff2'),
    url('fonts/aicon.ttf?gwz9im') format('truetype'),
    url('fonts/aicon.woff?gwz9im') format('woff'),
    url('fonts/aicon.svg?gwz9im#aicon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="aicon-"], [class*=" aicon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'aicon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

